          message:
            "CR de Julien C. suite \u00e0 la g\u00e9n\u00e9ration des attestations pour le p\u00e9rim\u00e8tre Looma (voir onglet documents)<br><br>Charline a contr\u00f4l\u00e9 que les attestations g\u00e9n\u00e9r\u00e9es en production \u00e9tait conformes aux mod\u00e8les fournis par le porteur de risque.<br><br><div>Am\u00e9liorations pr\u00e9vues :</div><div>De cette premi\u00e8re \u00e9tude, ressort 2 am\u00e9liorations n\u00e9cessaires et 1 bug \u00e0 corriger qui ont \u00e9t\u00e9 ajout\u00e9s au backlog produit:</div><div>- Dans le cas ou une prime Sans-suite (code S) est existante, mais post\u00e9rieure \u00e0 la date de fin du contrat ou de l\u2019adh\u00e9sion, alors l\u2019attestation peut \u00eatre g\u00e9n\u00e9r\u00e9e.</div><div>- Dans le cas ou une prime Sans-suite (code S) est existante, mais ant\u00e9rieure \u00e0 la date de fin de l\u2019attestation, alors dans un cas pr\u00e9cis que le service cotisations doit d\u00e9tailler, l\u2019attestation pourra \u00eatre g\u00e9n\u00e9r\u00e9e, pour les autres cas, elle ne le sera pas.</div><div>A noter : le type prime Sans-suite (code S) n\u2019\u00e9tait pas pris en compte avant cette mise en production.</div><div>- Bug : Dans le cas d\u2019un souscripteur avec plusieurs adresses email s\u00e9par\u00e9es par un point-virgule dans un champ email, il y a un pb de validation syntaxique des adresses email qui sera corrig\u00e9 rapidement (2 cas)</div><div><br></div><div>Suite \u00e0 l\u2019analyse des cas d\u2019attestation non g\u00e9n\u00e9r\u00e9e et de la m\u00e9thode employ\u00e9e pour r\u00e9aliser ces contr\u00f4les, des propositions doivent \u00eatre faites par le m\u00e9tier pour am\u00e9liorer l\u2019automate de g\u00e9n\u00e9ration afin de limiter les analyses manuelles, mais \u00e9galement des suggestions pour faciliter et rendre plus efficace ces analyses.</div><div><br></div><div>Point d\u2019attention: certaines attestations sont parties vers un apporteur LOOMA qui a son adresse email renseign\u00e9e en destinataire de l\u2019avis d\u2019\u00e9ch\u00e9ance de primes !!</div>",
