          date: "30/05/2022 09:00",
          author: null,
          message:
            "<div>Atelier de travail et point projet - voir CR ci-dessous et dans l'onglet documents</div><div><br></div>Sont pr\u00e9sents :<br>-\tNadia BOUSBIH<br>-\tJulien COMPAGNON<br><br>Absent : <br>-\tAucun<br><br>D\u00e9but de l\u2019atelier : 09h00<br>Dur\u00e9e de l\u2019atelier :   55 minutes<br><br>Ci-dessous, vous trouverez les sujets abord\u00e9s durant cet atelier :<br><br>Fonctionnalit\u00e9s \u00e0 r\u00e9habiliter suite \u00e0 leur suppression en fin d\u2019ann\u00e9e 2021 :<br><br>2 fonctionnalit\u00e9s ont \u00e9t\u00e9 supprim\u00e9es en fin d\u2019ann\u00e9e 2021 :<br><br>  - Saisie du motif lors d'une r\u00e9\u00e9dition d'une CTP depuis l\u2019outil Herm\u00e8s :<br>            D\u00e9cision : R\u00e9habilit\u00e9 - Il a \u00e9t\u00e9 d\u00e9cid\u00e9 de r\u00e9habiliter cette fonctionnalit\u00e9 en ajoutant une am\u00e9lioration, la s\u00e9lection depuis une liste du motif et non simplement une saisie libre <br>                        La gestion doit fournir une liste des motifs les plus r\u00e9currents<br>                        Un motif \u00ab Autre \u00bb avec saisie libre sera maintenu pour palier \u00e0 toute \u00e9ventualit\u00e9<br>                        Les motifs seront stock\u00e9s en base de donn\u00e9es pour qu\u2019ils soient facilement ajustables<br><br>  - Suppression d'une carte courrier r\u00e9\u00e9dit\u00e9e dans la journ\u00e9e (avant envoi \u00e0 CORUS)<br>D\u00e9cision : Non r\u00e9habilit\u00e9 - Cette fonctionnalit\u00e9 sera r\u00e9habilit\u00e9 ult\u00e9rieurement, lorsque tout le process de traitement quotidien (avec prise en compte du traitement de fin d\u2019ann\u00e9e) sera revu<br><br>Statut : En cours<br><br>Actions \u00e0 mener : <br>-\t09/05/2022 : @Nadia BOUSBIH doit communiquer la liste des motifs : FAIT le 18/05/2022<br>-\t09/05/2022 : @Julien COMPAGNON doit r\u00e9diger l\u2019US afin de planifier le d\u00e9veloppement, sachant que la liste des motifs n\u2019est pas un frein au d\u00e9veloppement : FAIT le 18/05/2022<br>-\t30/05/2022 : @Julien COMPAGNON doit ajouter ces US au prochain sprint : NON DEBUTE<br><br>Pr\u00e9sentation des d\u00e9veloppements en cours sur le sprint :<br>Explications sur l\u2019int\u00e9r\u00eat du passage par une API au lieu de ce qui existe \u00e0 ce jour.<br><br>Pr\u00e9sentation des d\u00e9veloppements pr\u00e9vus dans les 2 prochains sprints :<br>-\tHistorisation des demandes<br>-\tGestion du statut d\u2019une demande (basique 3 \u00e9tats)<br>-\tNouveau fonctionnement de l\u2019automate<br><br>Pr\u00e9sentation des \u00e9volutions pr\u00e9vues \u00e0 plus long terme : <br>-\tHistorisation Fen\u00eatre de gestion/contr\u00f4le des CTP \u00e0 valider (avant envoi)<br>-\tFen\u00eatre de suivi des demandes, d\u2019\u00e9dition des CTP<br>-\tSuivi du traitement des cartes valid\u00e9es par l\u2019automate<br>-\tGestion du statut de l\u2019envoi d\u2019un mail ou d\u2019un courrier via aiguilleur (niveau fin de suivi)<br><br>Statut : En cours<br><br>Actions \u00e0 mener : <br>Saisie du motif :<br>-\t30/05/2022 : @Nadia BOUSBIH doit d\u00e9finir et communiquer les contr\u00f4les et actions pouvant/devant \u00eatre r\u00e9aliser par la gestion avant la validation des CTP : NON DEBUTEE<br>A noter : Les filtres et donn\u00e9es n\u00e9cessaires sont requis afin de pouvoir construire la page de l\u2019outil de mani\u00e8re optimis\u00e9e pour la r\u00e9alisation de ces t\u00e2ches<br><br>-\t30/05/2022 : @Julien COMPAGNON doit faire une pr\u00e9sentation du module CTP au  marketing, afin qu\u2019il r\u00e9alise des maquettes : EN COURS - atelier pr\u00e9vu le 30/05/2022 \u00e0 15h00 <br><br>Prochain atelier :<br>Atelier non planifi\u00e9 mais pr\u00e9vu en juin en fonction de l\u2019avancement du produit<br>",
