          message:
            "Une mise en production de l\u2019API CTP vient d\u2019\u00eatre r\u00e9alis\u00e9e.<br><br>Voici le contenu de cette livraison technique importante qui va nous permettre d\u2019historiser correctement tout ce que traite l\u2019API.<br><br>Mise en production du 27/06/2022 :<br>US de la mise en production : https://app.clickup.com/t/2hqt0yn<br><br>Nouveaux d\u00e9veloppements :<br>\u2022\tRefonte de la base de donn\u00e9es + modification des entit\u00e9s<br>\u2022\tGestion du statut d'une demande, avec ajout de la notion de cat\u00e9gorie, de groupement d'\u00e9v\u00e8nements et de codification unique d'\u00e9v\u00e8nement<br>o\tLes \u00e9v\u00e8nements peuvent \u00eatre de nature SUCCES, ALERTE, ANOMALIE, ERREUR TECHNIQUE<br>o\tD\u00e9sormais chaque demande re\u00e7ue (sachant qu'elles sont toutes historis\u00e9es depuis la pr\u00e9c\u00e9dent mise en production), ont un \u00e9tat de traitement horodat\u00e9. Cet \u00e9tat est li\u00e9 \u00e0 une table recensant tous les \u00e9v\u00e8nements pouvant \u00eatre rencontr\u00e9 lors du traitement (la table sera compl\u00e9t\u00e9 au fil de l\u2019eau, des besoins (ajout de contr\u00f4le, de r\u00e8gles m\u00e9tiers, \u2026). Lors d'une future mise en production, nous aurons l'\u00e9tat mailJet ou Corus, lorsqu\u2019une demande d\u2019\u00e9mission de CTP sera transmise avec succ\u00e8s \u00e0 l\u2019aiguilleur.<br><br>Correctifs :<br>\u2022\tHistorisation en BDD de l'adresse postale lors de l'\u00e9mission d'un courrier<br>\u2022\tRefactorisation des environnements de d\u00e9veloppement des d\u00e9veloppeurs<br>\u2022\tCorrection de l\u2019outil de gestion des cartes Herm\u00e8s, afin qu\u2019il fonctionne avec la nouvelle base de donn\u00e9es<br><br>La r\u00e9ussite de cette mise en production sous-entend que nous allons pouvoir d\u00e9buter dans les prochains sprints :<br>-\tRenforcer le contr\u00f4les d\u2019\u00e9mission d\u2019une CTP<br>-\tRenforcer les traitements par un automate (lancement quotidien, report des demandes de CTP futur, \u2026)<br>-\tLes phases de restitution d\u2019informations de l\u2019API, incluant notamment le suivi de l\u2019activit\u00e9 des CTP depuis une interface graphique<br>o\tSuivi des demandes<br>o\tGestion de traitement d\u2019\u00e9mission des CTP par l\u2019automate \u2026<br>-\tle volet statistiques avec Gaetan<br>",
