      service: "DSI",
      applicant: null,
      status: "EN ATTENTE",
      description:
        "1 Contexte\nLe module de transmission des mails automatique constitut une brique suppl\u00e9mentaire dans le p\u00e9rim\u00e8tre DSN. Plusieurs niveaux permettront la transmission des informations relatives aux flux DSN. Le premier niveau est li\u00e9 aux donn\u00e9es techniques et sera en compl\u00e9ment du CRM (compte rendu m\u00e9tier). Le second niveau quant \u00e0 lui sera en lien avec la gestion des assur\u00e9s (affiliations\u2026).\n\n2 Objet\nD\u00e9veloppement d\u2019un programme permettant la transmission par mail automatique des retours m\u00e9tiers en lien avec les contr\u00f4les et traitements DSN.\nLe retour m\u00e9tier permettra d\u2019accompagner les clients dans la fiabilisation des donn\u00e9es v\u00e9hicul\u00e9es dans les flux. Une nouvelle interface (centre de contr\u00f4le) permettra le suivi des envois et le param\u00e9trage des mails types utilis\u00e9s.\nLes contacts fournis dans les flux seront utilis\u00e9s au niveau des segments \u00e9metteur (S10.G00.02) ou contact chez le d\u00e9clar\u00e9 (S20.G00.07).\n\n3 Objectif\nLa nouvelle brique doit permettre de faire le lien entre le SI d\u2019AGEO et le SI des d\u00e9clarants (soci\u00e9t\u00e9s et tiers d\u00e9clarants) afin de remonter les donn\u00e9es issues des contr\u00f4les DSN du SI d\u2019AGEO.\nL\u2019exp\u00e9rience montre que les transmissions des CRM DSN aboutissent peu \u00e0 des corrections sur les donn\u00e9es attendues, ainsi les informations remont\u00e9es auront pour but d\u2019am\u00e9liorer l\u2019accompagnement des clients et les donn\u00e9es dans le SI.\n\nLes mails transmis seront pr\u00e9d\u00e9finis et param\u00e9trable afin de cibler tous les niveaux de traitements des DSN et tous les types d\u2019anomalies rencontr\u00e9es.\n",
