      applicant: "Eric OZIEBALA",
      status: "EN ATTENTE",
      description:
        "1 Contexte\nMise en place de la gestion des suspensions de Contrat de type Invalidit\u00e9 2\u00e8me cat\u00e9gorie.\n\nLes salari\u00e9s en invalidit\u00e9 2\u00e8me cat\u00e9gorie doivent b\u00e9n\u00e9ficier du r\u00e9gime de frais de sant\u00e9 de l\u2019entreprise au m\u00eame titre que les salari\u00e9s actifs. (Changement d\u2019option que les salari\u00e9s actifs = > e-mail de Julie O.).\n\nBesoin (e-mail d\u2019Emmanuel GRANT (SODEXO) et Eric OZIEBALA (DG AGEO) :\nIdentifier via la DSN les salari\u00e9s concern\u00e9s tous les mois => pour AGEO proposer la mutuelle.\n\nP\u00e9rim\u00e8tre concern\u00e9 : toutes les soci\u00e9t\u00e9s g\u00e9r\u00e9es par AGEO\n\n2 R\u00e8gles de gestion\nLes salari\u00e9s en invalidit\u00e9 2\u00e8me cat\u00e9gorie doivent b\u00e9n\u00e9ficier du r\u00e9gime de frais de sant\u00e9 de l\u2019entreprise au m\u00eame titre que les salari\u00e9s actifs.\n\u00b7 Adh\u00e9sion mutuelle \u00e0 tout nouvel invalide sur des contrats sp\u00e9cifique.\nCela implique que les invalides 2\u00e8me cat b\u00e9n\u00e9ficient des m\u00eames droits de changement d\u2019option que les salari\u00e9s actifs.\n\n3 Sp\u00e9cificit\u00e9 li\u00e9e aux appels de cotisations\nM\u00eame mode op\u00e9ratoire que les contrats cong\u00e9s parentaux pour les futurs contrats en invalidit\u00e9 Cat. 2\n\nAgeo se charge de g\u00e9rer les cotisations directement avec le collaborateur en invalidit\u00e9. : pr\u00e9compte de la part patronale et appel de cotisation direct aupr\u00e8s de la personne invalide pour la part salariale.\n\nLes appels de cotisations sont envoy\u00e9s au CSP (\u00e0 Corinne CROISOT et Miguel MARQUES) par Laura C. (service cotisations collectives) concernant la part patronale pour les salari\u00e9s concern\u00e9s.",
