      description:
        "Optimisations pour am\u00e9liorer les temps de r\u00e9ponse et fluidifier l'utilisation de l'outil:\n\n1.  A l'int\u00e9gration des lignes dans ToolBox ne pas \u00e9craser la date de r\u00e9ception par la derni\u00e8re les IJSS si le document est encore \u00e0 l'\u00e9tat a traiter\n\n2.  Classer les documents en GED en mode asynchrone au clic afin que la gestionnaire n'attende pas la fin du traitement qui est important\n=> ANNULE ! car ne correspond pas \u00e0 la fa\u00e7on de travailler des gestionnaires\n\n3.  Optimisation du chargement des pani\u00e8res lorsqu'une gestionnaire travaille sur la pani\u00e8re d'une autre\n\n4.  Outil de suivi: avoir un bouton pour passer directement le dossier en indem (sans valider la r\u00e9ception de tous les documents)\n\n5.  Classer par d\u00e9faut les documents par ordre de date du plus ancien au plus r\u00e9cent selon la date de r\u00e9ception\n\n6.  Possibilit\u00e9 de s\u00e9lectionner plusieurs documents \u00e0 classer dans le m\u00eame dossier\n\n7.  Lors de s\u00e9lection d'un BPIJ et rattachement \u00e0 un dossier: g\u00e9n\u00e9rer le PDF et le classer en GED\n\n8.  Modification du nombre de r\u00e9sultats affich\u00e9s lors d'une recherche (Support NOVAXEL)\n\n9. Ne plus charger automatiquement le premier document de chaque dossier / gain de 2 secondes par dossier (Support NOVAXEL)",
