      service: "DIRECTION",
      applicant: null,
      status: "PRODUCTION",
      description:
        "Comme convenu lors de notre r\u00e9union du 14/03 avec Sandrine DEMONJEAN, nous avons fait un atelier avec Sandrine et Magali CHOUQUET afin de faire un point sur les diff\u00e9rentes \u00ab probl\u00e9matiques \u00bb que nous rencontrons depuis quelques semaines/mois (en compl\u00e9ment des diff\u00e9rents \u00e9changes). Avant de citer les diff\u00e9rentes actions \u00e0 mener, j\u2019ai pris la responsabilit\u00e9 d\u2019indiquer que l\u2019objectif \u00e9tait de cl\u00f4turer tous ces points avant notre r\u00e9union du 07/04/2022.\n\n\nCi-dessous les action \u00e0 mener :\n\n- @H\u00e9lios DUHAMEL : elles sont conscientes que le traitement des fichiers AGEO \uf0e8 SODEXO de 16h \u00e0 20h devient de plus en plus probl\u00e9matique. Sur ce sujet, Sandrine m\u2019a confirm\u00e9 qu\u2019il \u00e9tait important que les GP SODEXO aient les reportings Excel avant 07h30 le lendemain, je dois donc contacter le technique SODEXO pour savoir s\u2019il est possible d\u2019\u00e9mettre les fichiers d\u2019int\u00e9gration dans le logiciel paie SODEXO dans la nuit.\n\n- @H\u00e9lios DUHAMEL : sur le reporting, il y a eu deux probl\u00e8mes sur la non r\u00e9ception de certaines IJSS. Apr\u00e8s analyse, ces deux anomalies existent depuis l\u2019origine, j\u2019ai apport\u00e9 des explications sur la premi\u00e8re mais je dois faire un compte rendu sur la deuxi\u00e8me. A priori plus d\u2019anomalie depuis mes interventions.\n\n- @H\u00e9lios DUHAMEL @Julien GAUTIER : Suite \u00e0 ces deux anomalies, je dois transmettre un delta des IJSS manquantes depuis le 01/09/2021. Ce traitement est particuli\u00e8rement long je dois trouver une solution avec @Julien GAUTIER  afin que ma session TSE reste active plusieurs jours.\n\n- @H\u00e9lios DUHAMEL : m\u2019assurer que les IJSS subrog\u00e9s sont bien transmis dans les reportings.\n\n- Fait hier @H\u00e9lios DUHAMEL : sur les reportings et lorsqu\u2019 une IJSS provient de la 687 (no\u00e9mie) calculer le montant net avec une d\u00e9duction de 6.7% (CSG/CRDS).\n\n- @Marie-Christine REICHART : Concernant l\u2019 e-mail d\u2019une gestionnaire qui demande les d\u00e9comptes SS d\u2019un salari\u00e9 \u00e0 un GP SODEXO (16/03) , il faut communiquer \u00e0 nouveau aupr\u00e8s de du p\u00f4le SODEXO afin de ne pas demander ce document. @Marie-Christine REICHART, tu m\u2019as confirm\u00e9 que c\u2019\u00e9tait une erreur de la gestionnaire (\u00e7a arrive), cependant il faut utiliser Gest\u2019IP en lieu et place d\u2019une demande par e-mail. Lorsque cette communication est faite, Sandrine et Magali attendent un e-mail confirmant le discours \u00e0 tenir aupr\u00e8s de leurs GP sur ce sujet et r\u00e9pondre au passage au GP sur son e-mail du 16/03 (si ce n\u2019est pas encore fait)\n\n- @H\u00e9lios DUHAMEL : sur l\u2019espace entreprise et depuis la mise en place de Cloudflare par @Julien GAUTIER (politique de s\u00e9curit\u00e9), en g\u00e9n\u00e9ral \u00e0 la fin du mois les GP rencontrent une erreur 524 de Cloudflare car le temps de r\u00e9ponse d\u00e9passe les 90 secondes. Je dois donc intervenir sur cette partie afin d\u2019optimiser les temps de r\u00e9ponse. A priori d\u2019apr\u00e8s Magali cette erreur survient sur les d\u00e9clarations Mi-Temps Th\u00e9rapeutique, je vais donc prioriser cette partie.\n\n- @H\u00e9lios DUHAMEL : un GP \u00e0 une erreur lors de la d\u00e9claration d\u2019un arr\u00eat initial sur l\u2019espace entreprise (paternit\u00e9 avec des pi\u00e8ces jointes). J\u2019ai eu l\u2019accord de Sandrine et Magali pour contacter cette personne directement afin de r\u00e9soudre ce  probl\u00e8me.\n\n- @H\u00e9lios DUHAMEL : pour certaines DSIJ, la S\u00e9curit\u00e9 Sociale demande \u00e0 SODEXO de transmettre le RIB de la soci\u00e9t\u00e9. Je dois donc m\u2019assurer que nous transmettons bien les coordonn\u00e9es bancaires dans les flux (Magali va me fournir un cas).\n\n- @H\u00e9lios DUHAMEL : pour certaines DSIJ, la S\u00e9curit\u00e9 Sociale indique \u00e0 SODEXO que l\u2019adresse de l\u2019\u00e9tablissement n\u2019est pas pr\u00e9sente dans les flux, de m\u00eame que pour les RIB faire un retour \u00e0 SODEXO pour indiquer si oui ou non cette information est actuellement envoy\u00e9e.\n\n- @H\u00e9lios DUHAMEL : concernant le sujet des CR DSIJ de la S\u00e9curit\u00e9 Sociale re\u00e7u avec un mois de retard, j\u2019ai indiqu\u00e9 que j\u2019attendais une r\u00e9ponse de notre OPEDI (smalltox) pour savoir d\u2019o\u00f9 provient ce retard.\n\n- Fait hier  @H\u00e9lios DUHAMEL : fournir les coordonn\u00e9es de @Marie-Christine REICHART en lieu et place de @Olivier BERTRAND pour apporter des r\u00e9ponses sur certains dossiers.",
