            "R\u00e9cap H\u00e9lios:<br><br>\tT\u00e2ches termin\u00e9es :<br>- S\u2019assurer que les IJSS subrog\u00e9es sont bien transmis dans les reportings<br>- Sur les reportings et lorsqu\u2019une IJSS provient de la 687(no\u00e9mie) calculer le montant net avec une d\u00e9duction de 6.7% (CSG/CRDS)<br>- Fournir les coordonn\u00e9es de Marie-Christine REICHART en lieu et place de Olivier BERTRAND pour apporter des r\u00e9ponses sur certains dossiers.<br>- D\u00e9placement du traitement SODEXO apr\u00e8s 20h<br>- Le sujet des CR DSIJ de la S\u00e9curit\u00e9 Sociale re\u00e7u avec un mois de retard<br>- Faire un point sur les Flux SODEXO -&gt; AGEO pour la saisie du vendredi 18/08<br>- Transmission d\u2019un delta des IJSS non re\u00e7u depuis le 01/09/2021<br><br>T\u00e2ches en cours :<br>- Optimisation du site pour \u00e9viter les erreurs 524 (timeout 90 secondes)<br>- Pour certaines DSIJ, la S\u00e9curit\u00e9 Sociale indique \u00e0 SODEXO que l\u2019adresse de l\u2019\u00e9tablissement n\u2019est pas pr\u00e9sente dans les flux<br><br>T\u00e2ches \u00e0 faire :<br>- Concernant le courriel d\u2019une gestionnaire qui demande les d\u00e9comptes SS d\u2019un salari\u00e9 \u00e0 un GP SODEXO (16/03), il faut communiquer \u00e0 nouveau aupr\u00e8s de du p\u00f4le SODEXO afin de ne pas demander ce document. Sandrine et Magali attendent un e-mail confirmant le discours \u00e0 tenir aupr\u00e8s de leurs GP et r\u00e9pondre au passage au GP sur son e-mail du 16/03 (en cours)<br>- Faire un compte rendu sur le deuxi\u00e8me probl\u00e8me sur la non r\u00e9ception de certaines IJSS<br>- Contacter Amandine PARMENTIER suite erreur \u00e0 la d\u00e9claration d\u2019un arr\u00eat de travail (paternit\u00e9 avec PJ)<br>- Pour certaines DSIJ, la S\u00e9curit\u00e9 Sociale demande \u00e0 SODEXO de transmettre le RIB de la soci\u00e9t\u00e9<br>- Le taux IJSS concernant le multi-employeur, regarder les exemples de Magali<br>- Reporting des dossiers Pr\u00e9voyance sans IJSS au bout de 14 jours, reporting hebdo depuis le 1 janvier + rapprocher les CR SS<br>",
