      description:
        "Migration de la gestion des Gestip de Smalltox vers Coperlab\nMick avec Coperlab \u00e0 partir du 19/05/2022\n\nLe flux GESTIP est une liste structur\u00e9e par entreprise de salari\u00e9s pour lesquels le gestionnaire d\u2019assurance souhaite recevoir les bordereaux d\u2019indemnit\u00e9s journali\u00e8res \u00e9mis par la CNAM en cas d\u2019arr\u00eat de travail. Pour chaque salari\u00e9, une p\u00e9riode de couverture, pouvant porter sur une p\u00e9riode pass\u00e9e (r\u00e9troactivit\u00e9), doit \u00eatre d\u00e9finie.\n\nCe flux comporte les informations suivantes :\n- Cl\u00e9 d\u2019acc\u00e8s au service\n- Raison sociale du gestionnaire d\u2019assurance\n- Donn\u00e9es entreprise\n- Donn\u00e9es salari\u00e9\n- Type de couverture\n- P\u00e9riode de couverture\n\nGestionnaires d'assurance -> PUBLIFLUX -> Caisse Nationale d\u2019Assurance Maladie (CNAM)",
