            "Recette technique en cours:\n\nApr\u00e8s la fin de l'ex\u00e9cution du script en recette, nous allons transmettre les fichiers GESTIP \u00e0 COPERLAB pour qu'ils puissent valider l'int\u00e9grit\u00e9 des fichiers transmis.\n\nUne fois la recette valid\u00e9e c\u00f4t\u00e9 COPERLAB, nous lancerons la migration sur un seul souscripteur SODEXO (SODEXO SPORTS ET LOISIRS) pour que le m\u00e9tier puisse valider sa partie (petite pr\u00e9cision: nous ne pouvons recevoir les BPIJ qu'en production, le m\u00e9tier ne peut donc \"recetter\" qu'\u00e0 partir de ce moment l\u00e0).\n\nC'est pourquoi nous allons d\u00e9ployer uniquement pour SODEXO SPORTS ET LOISIRS, et apr\u00e8s validation du m\u00e9tier, nous migrerons le reste pour SODEXO.\n\n",
