          message:
            "CR Atelier Prest'iPrev - DSI - Prev AGEO - COPERLAB - 08/09/2022 : Voir le CR dans l'onglet document\n\nUn compl\u00e9ment suite \u00e0 un oubli de ma part, et, pour faire suite \u00e0 notre \u00e9change de fin de review :\n\nNBA demande si la fonctionnalit\u00e9 PP de MAJ des infos de paiement d\u2019un BPIJC cr\u00e9\u00e9 automatiquement par PP doit \u00eatre test\u00e9e et valid\u00e9e pour la d\u00e9cision de Go / No GO MEP.\nNous avions convenu que ce n\u2019\u00e9tait pas n\u00e9cessaire ce matin mais apr\u00e8s analyse, cela est n\u00e9cessaire car si les infos de paiement des BPIJC cr\u00e9\u00e9s en auto. par PP ne sont pas mises \u00e0 jour, le BPIJC va rester en statut \u00ab A r\u00e9gler \u00bb dans PP et les futurs BPIJSS li\u00e9s au m\u00eame dossier sinistre seront tous exclus pour motif \u00ab BPIJC pr\u00e9c\u00e9dent non r\u00e9gl\u00e9 \u00bb.\n\n\n",
          project_id: 182,
