          message:
            "Atelier Prest\u2019iPrev AGEO / COPERLAB 29/09/2022\n\nPr\u00e9sents :\n\u2022\tAgeo : Emmanuelle LE FUR (ELF), Lucille BEAUDOUX (LBE), Mick MOISSON (MMO), Zohra DAHMANI (ZDA), Gwendoline LAPLAGNE (GLA), H\u00e9lios DUHAMEL (HDU), Ariane DEROBERT (ADE), Quentin MOREAU (QMO)\n\u2022\tProxiad : Nordine BAZIZ (NBA)\n\u2022\tCoperlab : Christophe PETITHOMME (CPE)\n\nAbsents : Gautier ANTOINE (GAN), Teddy ECORCHARD (TEC)\n\nAbsents \u00e9voqu\u00e9s : Laurence BEAUCHAMP (LBP), Paul ROUSSEAU (PRO)\n\nAutomatisation paiement des PIJC de prolongation SODEXO HCR et SNRC -> Mise en production effectu\u00e9e le 21/09/2022\nBACK OFF AGEO\n\n\u2022\tZDA confirme que le d\u00e9ploiement du service \u00e0 d\u2019autres clients AGEO (dont KORIAN et INTERIM) est conditionn\u00e9 par la mise en \u0153uvre de la synchronisation automatis\u00e9e des donn\u00e9es \u00ab Contrats \u00bb AGEO <-> Prest\u2019iPrev.\n\nPREV AGEO\n\u2022\tELF et LBE ont d\u00e9but\u00e9 le chantier d\u2019am\u00e9lioration continue du dispositif en \u00e9tudiant les causes de rejet des BPIJSS les plus fr\u00e9quents.\n\n\n\nEt maintenant ? Que(s) chantier(s) prioriser ?\n\nLa charge de la mise en \u0153uvre des chantiers suivants est \u00e0 estimer pour le 03/10/2022 au plus tard :\n\n\u2022\tChantier Synchro des donn\u00e9es \u00ab Contrats \u00bb AGEO <-> Prest\u2019iPrev (Ne pas faire l\u2019amalgame avec DIGIPREV)\n\nHDU, MMO et CPE se r\u00e9unissent pour conceptualiser le m\u00e9canisme \u00e0 mettre en place et identifier les macros fonctionnalit\u00e9s \u00e0 mettre en \u0153uvre dans l\u2019optique de pouvoir estimer \u00ab grosses mailles \u00bb la charge du chantier.\n            Un retour se fait le 03/10\n\n\u2022\tChantier \u00ab Paiement des PIJC de prolongation KORIAN \u00bb (avec migration OPEDI Prest\u2019IJ SMALLTOX vers Prest\u2019iPrev)\n\n\u2022\tChantier \u00ab Paiement des PIJC de prolongation INTERIM \u00bb\n\n\u2022\tChantier \u00ab Instruction auto. des dossiers et 1er paiement SODEO HCR et SNRC \u00bb\n\n\nSont pr\u00e9cis\u00e9s \u00e9galement dans la pr\u00e9sentation de la feuille de route PP 22-23 d\u2019ELF les chantiers \u00ab Ouverture dossier & 1er paiement KORIAN \u00bb et \u00ab Outil de gestion PREV \u00bb, lesquels n\u2019ont pas \u00e9t\u00e9 abord\u00e9 en s\u00e9ance.\n\n\nCompta / finances\n\n\u2022\tQMO demande comment seront cr\u00e9\u00e9s et mis \u00e0 jour les donn\u00e9es des courtiers dans PP. CPE pr\u00e9cise que ce besoin est pris en compte dans le chantier de synchronisation auto. des donn\u00e9es \u00ab Contrats \u00bb AGEO <-> PP.\n\n\u2022\tQMO demande si des changements sont \u00e0 pr\u00e9voir sur les processus de reversement des prestations. CPE indique qu\u2019il n\u2019y aura pas d\u2019impact en ce sens.\n\nDSI AGEO\n\n\u2022\tHDU pr\u00e9cise que la migration d\u2019OPEDI (SMALLTOX vers Prest\u2019iPrev pour le PR Malakoff Humanis) d\u00e9marrera le 04/10. D\u00e8s que MMO aura macro estimer le chantier de synchronisation auto. des donn\u00e9es \u00ab Contrats \u00bb AGEO <-> PP.\n\n\u2022\tChantier \u00ab Arr\u00eat NOEMIE 686 \u00bb\n\nHDU alerte sur la n\u00e9cessit\u00e9 de sensibiliser le Codir sur ce chantier et de syst\u00e9matiquement en faire r\u00e9f\u00e9rence lors des diff\u00e9rents \u00e9changes et instances.\n            ELF remontera l\u2019alerte en Codir.\n\nAutres sujets abord\u00e9s :\n\n\u2022\tL\u2019administration des PR dans PP est de la responsabilit\u00e9 du service Juridique, cependant il est difficile voire impossible de retrouver pr\u00e9cis\u00e9ment la liste des actions de gestion d\u00e9l\u00e9gu\u00e9es et des r\u00e8gles m\u00e9tiers impos\u00e9es par le PR dans les protocoles de d\u00e9l\u00e9gation de gestion.\n\nCe sujet est \u00e0 approfondir. Une nouvelle recrue arrive le 03/10 et aura pour mission de mettre \u00e0 jour les protocoles. Un travail en commun avec la gestion Pr\u00e9voyance est \u00e0 pr\u00e9voir pour actualiser et faire valider les modalit\u00e9s de gestion convenues.\n\nConclusion \n\n\u2022\tL\u2019\u00e9quipe projet travaille sur les chiffrages \u00e0 fournir pour que la feuille de route PP 22-23 soit d\u00e9finie lors du Codir du 04/10.\n\nBien cordialement,\n\n \n \tChristophe PETITHOMME\n\nT. 05 57 28 36 44 \nM. 06 88 59 49 27\n\n\n",
          project_id: 182,
