            "Atelier Prest\u2019iPrev AGEO / COPERLAB 06/10/2022\n\nPr\u00e9sents :\n\u2022\tAgeo : Emmanuelle LE FUR (ELF), Lucille BEAUDOUX (LBE), Mick MOISSON (MMO), Gwendoline LAPLAGNE (GLA), H\u00e9lios DUHAMEL (HDU) , Teddy ECORCHARD (TEC)\n\u2022\tProxiad : Nordine BAZIZ (NBA)\n\u2022\tCoperlab : Christophe PETITHOMME (CPE)\n\nAbsents : Gautier ANTOINE (GAN), Zohra DAHMANI (ZDA), Ariane DEROBERT (ADE), Quentin MOREAU (QMO)\n\nAbsents \u00e9voqu\u00e9s : Laurence BEAUCHAMP (LBP), Paul ROUSSEAU (PRO)\n\n\nCODIR du 04/10 \u2013 D\u00e9cisions\n\n-\tChangement de strat\u00e9gie de conception de Prest\u2019iPrev -> Objectif : Avoir un version Prest\u2019iPrev commercialisable pour REAVIE 2023 (Octobre 2023)\n\nPour cela CPE identifie les fonctionnalit\u00e9s indispensables en vue de construire la roadmap 2023\n\n-\tChantiers prioritaires fin 2022\n\nAutomatisation paiement des PIJC de prolongation KORIAN puis Automatisation de l\u2019instruction des dossiers sinistres et 1er paiement SODEXO HCR et SNRC\n\nLe chantier Synchronisation des donn\u00e9es \u00ab Contrats \u00bb AGEO <-> Prest\u2019iPrev est d\u00e9prioris\u00e9 afin de laisser le temps \u00e0 l\u2019\u00e9quipe projet de construire une architecture cible solide et p\u00e9renne.\n\n-\tUn renfort de l\u2019\u00e9quipe projet avec un profil poss\u00e9dant de fortes comp\u00e9tences m\u00e9tiers dans le domaine de la pr\u00e9voyance a \u00e9t\u00e9 valid\u00e9. Ce profil va venir remplacer NBA qui va donc quitter l\u2019\u00e9quipe \u00e0 une date encore non d\u00e9termin\u00e9e \u00e0 ce jour.\n\nAutomatisation paiement des PIJC de prolongation KORIAN\n\n-\tCPE pr\u00e9cise que les d\u00e9veloppements sont en cours. Livraison pr\u00e9vue le 14/10/2022\n\n-\tLBE pr\u00e9cise qu\u2019il est n\u00e9cessaire de pr\u00e9parer l\u2019environnement de recette (MAJ donn\u00e9es TOOLBOX, int\u00e9gration des donn\u00e9es Souscripteurs et contrats, etc\u2026) afin de ne pas reproduire ce qui a \u00e9t\u00e9 fait d\u00e9but Ao\u00fbt 2022 pour SODEXO et ainsi perdre plusieurs jours de recette m\u00e9tier.\nA suivre avec MMO\nLa migration Prest\u2019IJ de SMALLTOX vers PP est en cours. Pas d\u2019alerte \u00e0 ce jour.\n\n-\tGLA s\u2019occupe de r\u00e9cup\u00e9rer les donn\u00e9es souscripteurs / contrats sous Excel afin de mettre \u00e0 jour l\u2019environnement de test.\n\n-\tCPE pr\u00e9cise \u00e0 GLA qu\u2019il avait \u00e9t\u00e9 indiqu\u00e9 que le chantier Synchronisation des donn\u00e9es \u00ab Contrats \u00bb AGEO <-> Prest\u2019iPrev \u00e9tait un pr\u00e9requis au d\u00e9ploiement de ce service \u00e0 KORIAN or ce chantier \u00e9tant d\u00e9prioris\u00e9, il sera n\u00e9cessaire de proc\u00e9der manuellement aux \u00e9ventuelles modifications dans PP.\n\n\nAutomatisation de l\u2019instruction des dossiers sinistres et 1er paiement SODEXO HCR et SNRC\n\n-\tCPE a identifi\u00e9 les nouvelles fonctionnalit\u00e9s et am\u00e9liorations \u00e0 impl\u00e9menter dans PP \u2013 Objectif de livraison avant la fin d\u2019ann\u00e9e 2022.\n\n-\tLes ateliers m\u00e9tiers ont d\u00e9but\u00e9 et plusieurs fonctionnalit\u00e9s sont en cours de conception.\n\nD\u00e9but des d\u00e9veloppement \u00e0 partir du 17/10/2022\n\n\nMCO et am\u00e9lioration(s)\n- Automatisation paiement des PIJC de prolongation\n\n\u2022\tLBE pr\u00e9cise qu\u2019une seule anomalie de remont\u00e9e des donn\u00e9es dans TOOLBOX sur un cas d\u2019usage non anticip\u00e9 est identifi\u00e9e \u00e0 ce jour. \nMMO a d\u00e9j\u00e0 identifi\u00e9 le probl\u00e8me, l\u2019a corrig\u00e9 et le correctif va \u00eatre d\u00e9ploy\u00e9 ce jour.\n\n\u2022\tDeux chantiers identifi\u00e9s viendront grandement am\u00e9liorer le dispositif lorsqu\u2019ils auront \u00e9t\u00e9 men\u00e9 :\n\u2022\tL\u2019automatisation de l\u2019instruction des dossiers sinistres et 1er paiement SODEXO HCR et SNRC.\n\u2022\tLa refonte du processus d\u2019inscription / d\u00e9sinscription au service Prest\u2019IJ (chantier qui aura \u00e9galement un impact financier et en lien avec le chantier \u00ab Arr\u00eat NOEMIE 686 \u00bb)\n\nOn estime que le taux de traitement auto. des BPIJSS sera grandement am\u00e9lior\u00e9 lorsque l\u2019automatisation de l\u2019instruction des dossiers sinistres et 1er paiement SODEXO HCR et SNRC ce chantier sera termin\u00e9. D\u00e8s lors, les causes de rejets des BPIJSS seront analys\u00e9es de mani\u00e8re plus approfondie.\n\nN\u00e9anmoins, le chantier de fiabilisation des donn\u00e9es CAFETERIA se poursuit.\n\n\nAutres sujets abord\u00e9s :\n\n\u2022\tCPE indique qu\u2019un nouvel environnement d\u00e9di\u00e9 aux tests m\u00e9tiers est mis en place. MMO doit intervenir de son c\u00f4t\u00e9 afin de \u00ab brancher \u00bb le SI AGEO de recette \u00e0 ce nouvel environnement.\n\n\u2022\tHDU demande si la mise en place d\u2019un environnement de d\u00e9monstration est pr\u00e9vu pour PP. CPE pr\u00e9cise que cela n\u2019\u00e9tait pas pr\u00e9vu \u00e0 date. HDU pr\u00e9cise que l\u2019on pourrait \u00e9tudier des solutions ensemble car la DSI AGEO en a \u00e9galement besoin.\n\n\u2022\tLe chantier commun de refonte des m\u00e9canismes de traitement de la DSN revient \u00e0 l\u2019ordre du jour.\n\nTEC pr\u00e9cise que celui-ci n\u2019a pas \u00e9t\u00e9 abandonn\u00e9 et que du temps y est consacr\u00e9. Un atelier d\u00e9di\u00e9 est \u00e0 reprogrammer par CPE.\n\n\u2022\tCPE pr\u00e9cise que la probl\u00e9matique du service juridique avec les protocoles de gestion et l\u2019identification des modalit\u00e9s de gestion sera \u00e0 l\u2019ordre du jour du prochain COSUI pr\u00e9vu le 11/10.\n\n\n\nBien cordialement,\n\n \n \tChristophe PETITHOMME\n\nT. 05 57 28 36 44 \nM. 06 88 59 49 27\n\n\n",
