            "Atelier Prest\u2019iPrev AGEO / COPERLAB 13/10/2022\n\nPr\u00e9sents :\n\u2022\tAgeo : Emmanuelle LE FUR (ELF), Lucille BEAUDOUX (LBE), Mick MOISSON (MMO), Zohra DAHMANI (ZDA), Gwendoline LAPLAGNE (GLA), H\u00e9lios DUHAMEL (HDU) , Teddy ECORCHARD (TEC)\n\u2022\tCoperlab : Christophe PETITHOMME (CPE)\n\nAbsents : Gautier ANTOINE (GAN), Ariane DEROBERT (ADE), Quentin MOREAU (QMO)\n\nAbsents \u00e9voqu\u00e9s : Laurence BEAUCHAMP (LBP), Paul ROUSSEAU (PRO), Julien COMPAGNON (JCO), Adrien RAOUT (ARA)\n\n\nCODIR du 04/10 \u2013 D\u00e9cisions\n\n-\tChangement de strat\u00e9gie de conception de Prest\u2019iPrev -> Objectif : Avoir un version Prest\u2019iPrev commercialisable pour REAVIE 2023 (Octobre 2023)\n\nCPE identifie les fonctionnalit\u00e9s indispensables en vue de construire la roadmap 2023 \u2013> Cartographie fonctionnelle en cours de cr\u00e9ation\n\n-\tUn renfort de l\u2019\u00e9quipe projet a \u00e9t\u00e9 valid\u00e9. Le profil souhait\u00e9 est en cours de d\u00e9finition par ELF.\nNBA quitte l\u2019\u00e9quipe projet le 19/10.\n\nAutomatisation paiement des PIJC de prolongation KORIAN\n\n-\tCPE pr\u00e9cise que les d\u00e9veloppements sont encore en cours. La livraison pr\u00e9vue initialement pr\u00e9vue le 14/10/2022 ne sera pas effectu\u00e9e. Elle doit avoir lieu le 17/10 ou le 18/10 au plus tard et ce afin que la recette m\u00e9tier puisse d\u00e9marrer le mercredi 19/10.\n\n-\tPr\u00e9paration \u00e0 la recette m\u00e9tier du 19/10 :\n\no\tHDU / MMO mettent en place une copie hebdomadaire des bases HERMES recette.\no\tLa migration Prest\u2019IJ de SMALLTOX vers PP s\u2019effectuera dans la nuit du 15/10 au 16/10. TEC effectuera les contr\u00f4les suite migration \u00e0 partir du 16/10 au matin (Dimanche)\no\tELF a demand\u00e9 aux services juridique et compta / finance d\u2019effectuer en urgence la cr\u00e9ation du PR Malakoff Humanis et de l\u2019interm\u00e9diaire d\u2019assurances Delta assurances dans le nouvel environnement de recette m\u00e9tier (UAT).\nSans cela, les RC ne peuvent pas \u00eatre cr\u00e9\u00e9s, les contrats import\u00e9s, \u2026\no\tGLA reste disponible pour effectuer le param\u00e9trage des RC d\u00e8s que cela est possible (cf. point pr\u00e9c\u00e9dent)\no\tCPE effectuera l\u2019int\u00e9gration automatique des souscripteurs et contrats KORIAN d\u00e8s que les RC seront cr\u00e9\u00e9s. (cf. point pr\u00e9c\u00e9dent)\n\n-\tZDA pr\u00e9cise que le p\u00e9rim\u00e8tre Souscripteurs et Contrats KORIAN est sujet \u00e0 de nombreuses modifications et que celles-ci seront report\u00e9es manuellement dans PP (en plus de HERMES et CAFETERIA).\n\n\nAutomatisation de l\u2019instruction des dossiers sinistres et 1er paiement SODEXO HCR et SNRC\n\n-\tLes ateliers m\u00e9tiers ont d\u00e9but\u00e9 et plusieurs fonctionnalit\u00e9s sont en cours de conception.\n\nD\u00e9but des d\u00e9veloppement \u00e0 partir du 17/10/2022 c\u00f4t\u00e9 COPERLAB\n\n-\tC\u00f4t\u00e9 AGEO, il est convenu que MMO d\u00e9bute le projet le 24/10 (d\u00e9calage d\u2019une semaine de 5 JH) et ce afin de permettre la mise en place de la nouvelle GED MOOVAPPS.\n\nSuivi des d\u00e9veloppements FAIT EN COURS A FAIRE\n\nLot\tFonctionnalit\u00e9s / pr\u00e9 requis technique\tDescription / remarque\tEstimation en JH\nD\u00e9v + recettes\tRecueil besoins\tConception\tD\u00e9v. + recette technique\tRecette m\u00e9tier + validation\nPorteur de risque\tParam\u00e9trage d\u00e9lai de d\u00e9claration sinistre\t\t4+1 = 5\t03/10\t14/10\t\t\nSouscripteurs\tParam\u00e9trage Automatisation(s) de gestion \t(aucune, paiement IJC de prolongation, Instruction dossier et 1er paiement)\t2+0,5 = 2,5\t03/10\t\t\t\nR\u00e9gime Compl\u00e9mentaire\tParam\u00e9trage franchise continu\t\t5 + 1,5 = 6,5\t10/10\t19/10\t\t\n\tParam\u00e9trage franchise discontinu sur ann\u00e9e glissante\t\t2 + 0,5 = 2,5\t10/10\t19/10\t\t\n\tParam\u00e9trage Salaire de r\u00e9f. SODEXO\tConversion brut / net avec tx = BAC, limite aux tranches, r\u00e8gle calcul du Sal. r\u00e9f journalier (forc\u00e9 365 jrs) fonction du r\u00e9gime : G\u00e9n\u00e9ral ou alsace)\t5 + 1,5 = 6,5\t17/10\t\t\t\n\tD\u00e9rogation d\u00e9lai de d\u00e9claration sinistre\t\t2 + 0,5 = 2,5\t03/10\t14/10\t\t\n\tParam\u00e9trage R\u00e8gles de calcul prestation compl\u00e9mentaire\tavec gestion de l\u2019anciennet\u00e9\t5 + 1,5 = 6,5\t\t\t\t\nBAC\tHistorisation des coefficients de conversion sal. brut / net \t\t0,5\t03/10\t\t\nGestion des d\u00e9clarations d\u2019arr\u00eats de travail (DAT)\t1\u00e8re partie du traitement auto. d\u2019une DAT\tMAJ Assur\u00e9, calcul dur\u00e9e de l\u2019arr\u00eat, gestion des rejets\t10 + 2,5 = 12,5\t14/10\t19/10\t\t\n\t2e partie du traitement auto. d\u2019une DAT\tContr\u00f4les droit \u00e0 l\u2019assurance / garantie, gestion rejets\t10 + 2,5 = 12,5\t\t\t\t\nAssur\u00e9\tConstituer l\u2019historique des salaires d\u2019un assur\u00e9\t\t\t\t\t\t\nDossier sinistre\tCr\u00e9ation auto. \u00e0 partir d\u2019une DAT\tAvec p\u00e9riode de suspension si TPA, gestion des rejets\t3 + 0,5 = 3,5\t\t\t\t\n\tCalcul Date d\u00e9but revalorisation\t\t2 + 0,5 = 2,5\t\t\t\t\n\tCalcul Date envoi CM\t\t1 + 0,5 = 1,5\t\t\t\t\n\tCalcul date prochain chgt RDC\t\t1 + 0,5 = 1,5\t\t\t\t\n\tCalcul du Salaire de r\u00e9f\u00e9rence contractuel\t\t3+1 = 4\t\t\t\t\n\tCalcul de l\u2019IJC contractuelle (montant journalier garanti)\t\t3 + 1,5 = 4,5\t\t\t\t\n\tMise \u00e0 dispo donn\u00e9es Dossiers au SI AGEO pour import\tavec gestion des rejets et retour ExternalId\t2+0,5 = 2,5\t\t\t\t\n\tAffichage DAT relatives au dossier sinistre\t(Confort pour PP quand pas utilis\u00e9)\t3+0,5 = 3,5\t\t\t\t\nBPIJSS\tEvolution process traitement auto. pour SOU inscrit \u00e0 l\u2019instruction auto. dossier et 1er paiement\tNouveaux motifs d\u2019exclusion ?? suite contr\u00f4le sur BPIJSS\n+ alertes gestion sans bloquer le paiement\t10+2,5 = 12,5\t\t\t\t\nBPIJC\tCr\u00e9ation auto. BPIJC avec montants PIJC calcul\u00e9s (1er paiement)\t\t5+1,5 = 6,5\t\t\t\t\nBI\tPerformance traitement des DAT\t\t\t\t\t\t\n\tEvol BI traitement des BPIJSS\t\t\t\t\t\t\nTotal\t78,5 + 21,5 = 100\t\n\nLot\tFonctionnalit\u00e9s / pr\u00e9 requis technique\tDescription / remarque\tEstimation en JH\nD\u00e9v + recettes\tRecueil besoins\tConception\tD\u00e9v. + recette technique\tRecette m\u00e9tier + validation\nCAFETERIA\tImport dossier sinistre PP\tgestion des rejets et retour id CAFET\t30JH\t\t\t\t\nTOOLBOX / GED\tArr\u00eat remont\u00e9e existante des dossiers depuis DAT\t\t\t\t\t\t\n\tClassement auto. si DAT trait\u00e9e en auto. PP\t\t\t\t\t\t\n\tDossier en traitement manuel \u00e0 remonter\t\t\t\t\t\t\n\n\n\nMCO et am\u00e9lioration(s)\n- Automatisation paiement des PIJC de prolongation\nRAS\nAutres sujets abord\u00e9s :\n\n\u2022\tHDU demande si la mise en place d\u2019un environnement de d\u00e9monstration est pr\u00e9vu pour PP. CPE pr\u00e9cise que cela n\u2019\u00e9tait pas pr\u00e9vu \u00e0 date. HDU pr\u00e9cise que l\u2019on pourrait \u00e9tudier des solutions ensemble car la DSI AGEO en a \u00e9galement besoin.\nCPE n\u2019a pas encore \u00e9chang\u00e9 sur le sujet avec l\u2019\u00e9quipe tech COPERLAB\n\n\u2022\tLe chantier commun de refonte des m\u00e9canismes de traitement de la DSN revient \u00e0 l\u2019ordre du jour.\nTEC pr\u00e9cise que celui-ci n\u2019a pas \u00e9t\u00e9 abandonn\u00e9 et que du temps y est consacr\u00e9. Un point a \u00e9t\u00e9 r\u00e9alis\u00e9 avec JCO et ARA.\nCPE reviendra sur ce projet lorsque la cartographie fonctionnelle de PP en version MVP commercialisable sera effectu\u00e9e.\n\n\u2022\tCPE pr\u00e9cise que la probl\u00e9matique du service juridique avec les protocoles de gestion et l\u2019identification des modalit\u00e9s de gestion sera \u00e0 l\u2019ordre du jour du prochain COSUI pr\u00e9vu le 11/10.\n\n\n\nBien cordialement,\n\n \n \tChristophe PETITHOMME\n\nT. 05 57 28 36 44 \nM. 06 88 59 49 27\n\n\n",
