            "Atelier Prest\u2019iPrev AGEO / COPERLAB 10/11/2022\n\nPr\u00e9sents :\n\u2022\tAgeo : Emmanuelle LE FUR (ELF), Mick MOISSON (MMO), Gwendoline LAPLAGNE (GLA), Teddy ECORCHARD (TEC), Lucille BEAUDOUX (LBE), H\u00e9lios DUHAMEL (HDU)\n\u2022\tCoperlab : Christophe PETITHOMME (CPE)\n\nAbsents : Gautier ANTOINE (GAN), Ariane DEROBERT (ADE), Quentin MOREAU (QMO), Zohra DAHMANI (ZDA), Claudia CORNU (CCO)\n\nAbsents \u00e9voqu\u00e9s : Laurence BEAUCHAMP (LBP), Paul ROUSSEAU (PRO), Julien COMPAGNON (JCO), Adrien RAOUT (ARA)\n\n\nCODIR du 03/11 \u2013 D\u00e9cisions\n\n-\tStrat\u00e9gie de conception de Prest\u2019iPrev -> Objectif : Avoir un version Prest\u2019iPrev commercialisable pour REAVIE 2023 (Octobre 2023)\n\nCPE doit \u00e9tablir la cartographie fonctionnelle cible et estimer la charge du \u00ab reste \u00e0 faire \u00bb  pour sa mise en \u0153uvre au plus vite.\n\n-\tUn renfort de l\u2019\u00e9quipe projet est envisag\u00e9. Le profil souhait\u00e9 est en cours de d\u00e9finition par ELF et la recherche de candidat a d\u00e9but\u00e9.\nLe budget allou\u00e9 doit encore \u00eatre valid\u00e9.\nAutomatisation paiement des PIJC de prolongation KORIAN\n\n\u2022\tLa recette m\u00e9tier du projet migration de GED MOOVAPPS est prioritaire par rapport \u00e0 la recette du lot \u00ab KORIAN \u00bb.\nLBE reprendra la recette d\u00e8s le lundi 14/11. L\u2019objectif est d\u2019avoir une d\u00e9cision Go / No Go pour le 18/11 et \u00e9ventuellement la mise en production le 23/11.\n\n3 anomalies d\u00e9tect\u00e9es \u00e0 ce jour : 1 a d\u00e9j\u00e0 \u00e9t\u00e9 corrig\u00e9e, 1 cl\u00f4tur\u00e9e car semble \u00eatre un cas isol\u00e9, est non reproduite (DA57 \u2013 double paiement CAFET), et la derni\u00e8re est v\u00e9rifi\u00e9e et est donc \u00e0 corriger (DA58 \u2013 Erreur calcul Date envoi CM).\n            CPE traite cette derni\u00e8re anomalie pour correction et revient vers l\u2019\u00e9quipe projet.\n\n\u2022\tLes souscripteurs et contrats KORIAN ont \u00e9t\u00e9 inject\u00e9 en production. Des contr\u00f4les et modifications seront effectu\u00e9s par GLA.\nIl restera \u00e0 mettre \u00e0 jour les souscripteurs KORIAN avec les donn\u00e9es de regroupement et d\u2019inscription au service pour le 1er lot automatiquement lors de la MEP.\nAutomatisation de l\u2019instruction des dossiers sinistres et 1er paiement SODEXO HCR et SNRC\n\n\u2022\tLes DAT issues de la DSN seront directement exclues par l\u2019automate de traitement car elles ne sont pas \u00e0 exploiter pour SODEXO HCR et SNRC.\n\nSuivi des d\u00e9veloppements FAIT EN COURS A FAIRE\n\nLot\tFonctionnalit\u00e9s / pr\u00e9 requis technique\tDescription / remarque\tRecueil besoins\tConception\tD\u00e9v. + recette technique\tRecette m\u00e9tier + validation\nPorteur de risque\tParam\u00e9trage d\u00e9lai de d\u00e9claration sinistre\t\t03/10\t14/10\t10/11\t\nSouscripteurs\tParam\u00e9trage Automatisation(s) de gestion \t(aucune, paiement IJC de prolongation, Instruction dossier et 1er paiement)\t03/10\t24/11\t\t\nR\u00e9gime Compl\u00e9mentaire\tParam\u00e9trage franchise continu\t\t10/10\t19/10\t10/11\t\n\tParam\u00e9trage franchise discontinu sur ann\u00e9e glissante\t\t10/10\t19/10\t10/11\t\n\tParam\u00e9trage du calcul du Salaire de r\u00e9f. SODEXO\tConversion brut / net avec tx = BAC, limite aux tranches, r\u00e8gle calcul du Sal. r\u00e9f journalier (forc\u00e9 365 jrs) fonction du r\u00e9gime : G\u00e9n\u00e9ral ou alsace)\t17/10\t24/11\t\t\n\tD\u00e9rogation d\u00e9lai de d\u00e9claration sinistre\t\t03/10\t14/10\t10/11\t\n\tParam\u00e9trage R\u00e8gles de calcul prestation compl\u00e9mentaire\tavec gestion de l\u2019anciennet\u00e9\t03/10\t08/11\t02/12\t\nBAC\tHistorisation des coefficients de conversion sal. brut / net \t\t03/10\t20/10\t\nGestion des d\u00e9clarations d\u2019arr\u00eats de travail (DAT)\t1\u00e8re partie du traitement auto. d\u2019une DAT\tMAJ Assur\u00e9, calcul dur\u00e9e de l\u2019arr\u00eat, gestion des rejets\t14/10\t19/10\t02/12\t\n\t2e partie du traitement auto. d\u2019une DAT\tContr\u00f4les droit \u00e0 l\u2019assurance / garantie, gestion rejets\t\t19/10\t02/12\t\n\tMise \u00e0 disposition des DAT trait\u00e9es\t\t\t10/11\t02/12\t\nAssur\u00e9\tConstituer l\u2019historique des salaires d\u2019un assur\u00e9\t\t31/10\t24/11\t\t\nDossier sinistre\tCr\u00e9ation auto. \u00e0 partir d\u2019une DAT\tAvec p\u00e9riode de suspension si TPA, gestion des rejets\t\t10/11\t02/12\t\n\tCalcul Date d\u00e9but revalorisation\t\t\t19/10\t02/12\t\n\tCalcul Date envoi CM\t\t\t\t19/10\t\n\tCalcul date prochain chgt RDC\t\t\t19/10\t02/12\t\n\tCalcul du Salaire de r\u00e9f\u00e9rence contractuel\t\t\t\t\t\n\tCalcul de l\u2019IJC contractuelle (montant journalier garanti)\t\t\t\t\t\n\tMise \u00e0 dispo donn\u00e9es Dossiers au SI AGEO pour import\tavec gestion des rejets et retour ExternalId\t\t\t\t\n\tAffichage DAT relatives au dossier sinistre\t(Confort pour PP quand pas utilis\u00e9)\t\t\t\t\nBPIJSS\tEvolution process traitement auto. pour SOU inscrit \u00e0 l\u2019instruction auto. dossier et 1er paiement\tNouveaux motifs d\u2019exclusion ?? suite contr\u00f4le sur BPIJSS\n+ alertes gestion sans bloquer le paiement\t\t\t\t\nBPIJC\tCr\u00e9ation auto. BPIJC avec montants PIJC calcul\u00e9s (1er paiement)\t\t\t\t\t\nBI\tPerformance traitement des DAT\t\t\t\t\t\n\tEvol BI traitement des BPIJSS\t\t\t\t\t\n\nAGEO - Actions \u00e0 r\u00e9aliser\n\nLot\tFonctionnalit\u00e9s / pr\u00e9 requis technique\tDescription / remarque\tRecueil besoins\tConception\tD\u00e9v + Recette technique\tRecette m\u00e9tier + validation\nCAFETERIA\tImport dossier sinistre PP\tgestion des rejets et retour id CAFET\t25/10\t\t\t\nTOOLBOX / GED\tR\u00e9ception des DAT trait\u00e9es (avec motif exclusion PP le cas \u00e9ch\u00e9ant)\t\t\t10/11\t02/12\t\n\tInterpr\u00e9tation des DAT trait\u00e9es PP pour remat TOOLBOX / GED\t\t\t18/11\t\t\n\tImport dossier sinistre PP\t\t\t\t\t\nD\u00e9cla SODEXO\tTransmission des revenus de l\u2019assur\u00e9\t\t\t\t\t\n\n\n\nMCO et am\u00e9lioration(s)\n- Automatisation paiement des PIJC de prolongation\n\u2022\tDes doublons d\u2019inscriptions GESTIP ont \u00e9t\u00e9 d\u00e9tect\u00e9. Le BPIJSS sont re\u00e7us en double pour les assur\u00e9s concern\u00e9s. TEC effectue un nouveau contr\u00f4le de doublon pour les identifier et les corriger.\n\n\u2022\tLes demandes d\u2019assistance (DA) \u00e9mises par les utilisateurs ne sont plus int\u00e9gr\u00e9es dans le fichier Excel de suivi. CPE prend en charge le probl\u00e8me.\nAutres sujets abord\u00e9s :\n\u2022\tSODEXO - Automatiser la gestion des TPA : ELF prend en charge le sujet\n\u2022\tSODEXO \u2013 Emission d\u2019une alerte en cas d\u2019interruption paiement s\u00e9cu : ELF, LBE et HDU se r\u00e9unissent pour \u00e9changer sur le sujet en partant d\u2019une demande de pr\u00e9cision rest\u00e9e sans r\u00e9ponse \u00e0 ce jour de la part des \u00e9quipes de SODEXO \u2013 Sujet \u00e0 remettre \u00e0 l\u2019ordre du jour. \n\n\u2022\tHDU demande si la mise en place d\u2019un environnement de d\u00e9monstration est pr\u00e9vu pour PP. CPE pr\u00e9cise que cela n\u2019\u00e9tait pas pr\u00e9vu \u00e0 date. HDU pr\u00e9cise que l\u2019on pourrait \u00e9tudier des solutions ensemble car la DSI AGEO en a \u00e9galement besoin.\nCPE n\u2019a pas encore \u00e9chang\u00e9 sur le sujet avec l\u2019\u00e9quipe tech COPERLAB\n\n\u2022\tLe chantier commun de refonte des m\u00e9canismes de traitement de la DSN revient \u00e0 l\u2019ordre du jour.\nUn atelier d\u00e9di\u00e9 est programm\u00e9 le 14/11/2022 16h.\n\n\u2022\tCPE pr\u00e9cise que la probl\u00e9matique du service juridique avec les protocoles de gestion et l\u2019identification des modalit\u00e9s de gestion sera \u00e0 l\u2019ordre du jour du prochain COSUI pr\u00e9vu le 11/10.\n\n\n\nBien cordialement,\n\n \n \tChristophe PETITHOMME\n\nT. 05 57 28 36 44 \nM. 06 88 59 49 27\n\n\n",
