        "Voici le compte rendu de notre r\u00e9union de ce jour ayant pour objet : MODIFICATION DU PONT ADHOC-HERMES SUITE AU CHANGEMENT DES REGLES DE RETROCESSION DES COMMISSIONS IARD\n \nParticipants :\n1.\tEmilie PICCINOTTI\n2.\tAntoine GIACOMOTTO\n3.\tAndy BAGUETTE\n4.\tPaul ROUSSEAUX\n5.\tBaptiste HENRY\n6.\tQuentin MOREAU\n \n \n \nRappel de la r\u00e8gle de partage existante :\n \nToute les commissions des contrats du m\u00e9tier IARD sont inject\u00e9es sur Herm\u00e8s. Herm\u00e8s a pour r\u00e8gle de partager automatiquement ces commissions en 2 :\n7.\t50% en commission d\u2019acquisition\n8.\t50% en commission de gestion\n \nSeuls les honoraires et frais de dossiers d\u00e9rogent \u00e0 cette r\u00e8gle. Ils ne sont pas partag\u00e9s en 2. La r\u00e8gle d\u2019injection des honoraires est la suivante : 100% de la commission Globale sur Adhoc = 100% de la commission d\u2019acquisition sur Herm\u00e8s et 0% en com de gestion.\n \n \n1.\tCette r\u00e8gle de partage 50/50 entre acquisition et gestion vient d\u2019\u00eatre revue courant 2022 pour que les nouveaux contrats prenant effet \u00e0 partir du 01/01/2022 aient leur commissions partag\u00e9es de la mani\u00e8re suivante :\n1.\tAcquisition = 65%\n2.\tGestion = 35%\n2.\tLe probl\u00e8me qui se pose, si l\u2019on ajoute simplement une nouvelle r\u00e8gle pour les contrats prenant effet \u00e0 partir du 01/01/2022, est que les contrats historiques transf\u00e9r\u00e9s au-del\u00e0 de cette date entreraient automatiquement sur ce nouveau param\u00e9trage. Ce que l\u2019on ne souhaite pas. Il devront continuer d\u2019\u00eatre partag\u00e9s \u00e0 50/50.\n \n \n \n \nCompte tenu de ces \u00e9l\u00e9ments, il a \u00e9t\u00e9 convenu de proc\u00e9der aux modifications suivantes :\n \n1.\tR\u00e8gle de partage dans le pont\n \n3.\tAnnuler la r\u00e8gle qui consiste \u00e0 partager sur Herm\u00e8s lors de leurs injections, toute les commissions IARD \u00e0 50% en Acquisition et 50% en Gestion\n \n4.\tAttention \u00e0 ne pas toucher au param\u00e9trage des autres types de contrats et commissions(pr\u00e9voyance, sant\u00e9)\n \n5.\tParam\u00e9trer le pont de mani\u00e8re \u00e0 ce que le partage de commission soit strictement celui param\u00e9tr\u00e9 sur ADHOC\n1.\tExemple : sur Adhoc contrat est param\u00e9tr\u00e9 \u00e0 65% de la com globale pour AGEO RISKS CONSEIL PARIS.\n2.\tAlors, 100% de la com globale devra \u00eatre inject\u00e9e dans Herm\u00e8s. Le nouveau param\u00e9trage de l\u2019affaire d\u00e9coupera automatiquement cette commission de la mani\u00e8re suivante : 65% en commission d\u2019acquisition et 35% en commission de gestion.\n \n6.\tUne r\u00e8gle de d\u00e9duction devra \u00eatre op\u00e9r\u00e9e sur pour que la commission de gestion soit syst\u00e9matiquement renseign\u00e9e.\n1.\tExemple : si l\u2019apporteur est param\u00e9tr\u00e9 \u00e0 80% du globale, alors le reste de la commission globale arrivera en commission de gestion, soit 20%.\n \n \n2.\tModification du param\u00e9trage de commissions sur tous les contrats IARD Herm\u00e8s\n \n7.\tL\u2019ensemble des contrats IARD actifs sur Herm\u00e8s devront \u00eatre modifi\u00e9s de la mani\u00e8re suivante :\n1.\tAncien param\u00e9trage : Date de cl\u00f4ture \u00e0 int\u00e9grer\n2.\tNouveau param\u00e9trage : Date d\u2019ouverture \u00e0 int\u00e9grer + param\u00e9trer la quote part de commission en % du GLOBAL (Ancienne quote part en % d\u2019Acquisition \u00e0 diviser par 2)\n \n8.\tLe service financier adressera \u00e0 Andy un fichier donnant les modifications de param\u00e9trage \u00e0 effectuer sur chaque contrats IARD\n \n \n3.\tModification du param\u00e9trage des apporteurs sur ADHOC\n \n9.\tUne modification de param\u00e9trage des apporteurs et de leurs quote part devra \u00eatre effectu\u00e9e sur ADHOC par l\u2019\u00e9quipe IARD\n \n \n4.\tR\u00e9gularisation des commissions 2022 revers\u00e9es sur un mauvais taux\n \n10.\tUne fois cette nouvelle r\u00e8gle mise en production et les contrats modifi\u00e9s sur Herm\u00e8s, Emilie donnera \u00e0 Paul la liste exhaustive des contrats sur lesquels une r\u00e9gularisation \u00e0 partir du 01/01/2022 est \u00e0 effectuer au niveau des commissions r\u00e9troc\u00e9d\u00e9es.\n \n11.\tPaul proc\u00e8dera \u00e0 ces r\u00e9gularisations de mani\u00e8re manuelle.\n \n12.\tBaptiste prendra en compte ces r\u00e9gularisations dans les SIG et forecast 2022.\n \n \n5.\tCommunication aux filiales sur le nouveau syst\u00e8me (% du Global)\n \n13.\tUn mail sera adress\u00e9 aux filiales IARD par Quentin afin de leur pr\u00e9ciser que dor\u00e9navant leurs commissions IARD apparaitront en % de la commission Globale dans leur bordereaux de commissions\n \n \nConclusion :\n \nPour le moment nous attendons donc tous le d\u00e9lai de d\u00e9veloppement de la part d\u2019Andy.\nComme pr\u00e9cis\u00e9 oralement, aucune de ces modifications de param\u00e9trages tant sur Adhoc que sur Herm\u00e8s, ne devra \u00eatre effectu\u00e9e tant que la nouvelle r\u00e8gle sur le pont ne sera pas mise en production.\nLes services informatiques, IARD et Commissions devront se coordonner pour \u00e9viter tout \u00ab frottement \u00bb dans les injections.",
