            "De : Gautier ANTOINE \nEnvoy\u00e9 : mercredi 14 d\u00e9cembre 2022 11:10\n\u00c0 : Quentin MOREAU <quentin.moreau@ageo.fr>; Andy BAGUETTE <andy.baguette@ageo.fr>; Emilie PICCINOTTI <emilie.piccinotti@ageo.fr>; Paul ROUSSEAUX <paul.rousseaux@ageo.fr>\nCc : Antoine GIACOMOTTO <antoine.giacomotto@ageo.fr>; H\u00e9lios DUHAMEL <helios.duhamel@ageo.fr>; Baptiste HENRY <baptiste.henry@ageo.fr>; Commissions <Commissions@ageo.fr>; V\u00e9ronique VESSELLE <veronique.vesselle@ageo.fr>; Julie FAICT <julie.faict@ageo.fr>; Pascal PETIT <pascal.petit@ageo.fr>; Julien COMPAGNON (julien.compagnon@ageo.fr) <julien.compagnon@ageo.fr>\nObjet : RE: Modification pont ADHOC-HERMES\n\nBonjour Quentin,\n\nNous n\u2019avons toujours pas re\u00e7u le fichier Excel, suite \u00e0 un \u00e9change avec les \u00e9quipes techniques, et au vu des cong\u00e9s de fin d\u2019ann\u00e9e et de la charge de travail, il apparait que nous ne pourrons garantir le bon traitement de ta demande avant le 31/12 si nous n\u2019avons pas ce fichier d\u2019ici fin de semaine.\n\nMe tenant \u00e0 votre disposition pour tout renseignement compl\u00e9mentaire.\n\nCordialement.\n\n\n\n \n\tGautier ANTOINE\nDirection des Syst\u00e8mes d\u2019Information\nChef de projet\n \n14, rue Joliot Curie CS 30248\n51010 Ch\u00e2lons-en-Champagne Cedex\n\nT.    03 26 26 64 71\n\n",
