            "De : Quentin MOREAU <quentin.moreau@ageo.fr> \nEnvoy\u00e9 : mercredi 14 d\u00e9cembre 2022 12:42\n\u00c0 : Gautier ANTOINE <gautier.antoine@ageo.fr>; Andy BAGUETTE <andy.baguette@ageo.fr>; Emilie PICCINOTTI <emilie.piccinotti@ageo.fr>; Paul ROUSSEAUX <paul.rousseaux@ageo.fr>\nCc : Antoine GIACOMOTTO <antoine.giacomotto@ageo.fr>; H\u00e9lios DUHAMEL <helios.duhamel@ageo.fr>; Baptiste HENRY <baptiste.henry@ageo.fr>; Commissions <Commissions@ageo.fr>; V\u00e9ronique VESSELLE <veronique.vesselle@ageo.fr>; Julie FAICT <julie.faict@ageo.fr>; Pascal PETIT <pascal.petit@ageo.fr>; Julien COMPAGNON <julien.compagnon@ageo.fr>\nObjet : RE: Modification pont ADHOC-HERMES\n\nBonjour Emilie,\n\nComme vu ensemble l\u2019autre jour, nous avons absolument besoin de votre fichier dans lequel apparaitront toute les affaires prenant effet en 2022 concern\u00e9es par cette mise \u00e0 jour.\nComme le pr\u00e9cise Gautier ci-dessous, c\u2019est urgent stp.\n\nUne fois ce fichier en notre possession, Paul le travaillera aussit\u00f4t pour donner \u00e0 Andy le param\u00e9trage exact \u00e0 appliquer sur Herm\u00e8s.\n\nMerci \u00e0 toi\n\nCordialement,\n\n \n\tQuentin MOREAU\nDirecteur Administratif et Financier\n \n14, rue Joliot Curie CS 30248\n51010 Ch\u00e2lons en Champagne Cedex\n\nT.    03 26 26 99 79   |   quentin.moreau@ageo.fr\nM.  06 48 19 18 93\n\n      www.ageo.fr\n\n",
