            "De retour de cong\u00e9s je prends des nouvelles aupr\u00e8s des \u00e9quipes et il apparait que nous n'avons toujours pas re\u00e7u le fichier Excel, nouvelle relance \u00e0 Quentin:<br><br>De : Gautier ANTOINE <br>Envoy\u00e9 : mardi 3 janvier 2023 10:11<br>\u00c0 : Quentin MOREAU <quentin.moreau@ageo.fr><br>Cc : Antoine GIACOMOTTO <antoine.giacomotto@ageo.fr>; Pascal PETIT <pascal.petit@ageo.fr>; H\u00e9lios DUHAMEL <helios.duhamel@ageo.fr>; Julien COMPAGNON <julien.compagnon@ageo.fr>; Andy BAGUETTE <andy.baguette@ageo.fr><br>Objet : RE: Modification pont ADHOC-HERMES<br><br>Bonjour Quentin,<br><br>Je rentre de cong\u00e9s et les \u00e9quipes m\u2019indiquent ne toujours pas avoir re\u00e7u ce fameux fichier Excel ?<br>Me tenant \u00e0 votre disposition pour tout renseignement compl\u00e9mentaire.<br><br>Cordialement.<br><br><br> <br>\tGautier ANTOINE<br>Direction des Syst\u00e8mes d\u2019Information<br>Chef de projet<br> <br>14, rue Joliot Curie CS 30248<br>51010 Ch\u00e2lons-en-Champagne Cedex<br><br>T.    03 26 26 64 71<br><br>\t\t <br>www.ageo.fr<br> <br></andy.baguette@ageo.fr></julien.compagnon@ageo.fr></helios.duhamel@ageo.fr></pascal.petit@ageo.fr></antoine.giacomotto@ageo.fr></quentin.moreau@ageo.fr>",
