          message:
            "R\u00e9ponse de Quentin suite \u00e0 ma relance:&nbsp;<br>--------------------------------------------------------------------------------------------------------<br>De : Quentin MOREAU <quentin.moreau@ageo.fr> <br>Envoy\u00e9 : jeudi 5 janvier 2023 14:37<br>\u00c0 : Gautier ANTOINE <gautier.antoine@ageo.fr>; Emilie PICCINOTTI <emilie.piccinotti@ageo.fr><br>Cc : Antoine GIACOMOTTO <antoine.giacomotto@ageo.fr>; Pascal PETIT <pascal.petit@ageo.fr>; H\u00e9lios DUHAMEL <helios.duhamel@ageo.fr>; Julien COMPAGNON <julien.compagnon@ageo.fr>; Andy BAGUETTE <andy.baguette@ageo.fr>; Paul ROUSSEAUX <paul.rousseaux@ageo.fr><br>Objet : RE: Modification pont ADHOC-HERMES<br><br>Bonjour Gautier,<br><br>Notre demande, d\u2019Antoine et moi-m\u00eame, pour ce d\u00e9veloppement date de Juillet.<br><br>Si cela ne tenait qu\u2019\u00e0 moi, vous auriez eu le fichier d\u00e8s octobre. Malheureusement ce fichier de contrats concern\u00e9s doit \u00eatre travaill\u00e9 manuellement, contrats par contrats, par le service IARD qui est en plein rush depuis Novembre.<br><br>Donc h\u00e9las, Octobre \u00e9tait d\u00e9j\u00e0 trop tard pour engager le projet. Pas pour mon service mais pour celui d\u2019Antoine.<br><br>J\u2019ai fait le point encore hier avec Antoine et Emilie qui sont toujours sur ce fichier. Je t\u2019invite \u00e0 interroger directement Emilie si tu as besoin de plus d\u2019informations.<br><br><br>En attendant c\u00f4t\u00e9 compta, je te confirme que je me retrouve \u00e0 devoir recalculer les commissions IARD 2022 de toute nos filiales, manuellement.<br>Et nous allons devoir r\u00e9gulariser une ann\u00e9e compl\u00e8te en terme de flux financiers, ce qui va \u00e9galement avoir des incidences sur la tr\u00e9sorerie des filiales et celle d\u2019AGEO.<br><br>Si cette modification n\u2019est toujours pas en production pour les paiements des filiales qui auront lieu fin janvier, alors oui \u00e7a va \u00e9norm\u00e9ment nous compliquer la t\u00e2che en compta. Je suis donc autant emb\u00eat\u00e9 que vous et j\u2019esp\u00e8re que les \u00e9quipes d\u2019Antoine vont pouvoir nous fournir ce fichier dans les tout prochains jours.<br><br>Cordialement,<br><br> <br>\tQuentin MOREAU<br>Directeur Administratif et Financier<br> <br>14, rue Joliot Curie CS 30248<br>51010 Ch\u00e2lons en Champagne Cedex<br><br>T.    03 26 26 99 79   |   quentin.moreau@ageo.fr<br>M.  06 48 19 18 93<br><br>      www.ageo.fr<br><br></paul.rousseaux@ageo.fr></andy.baguette@ageo.fr></julien.compagnon@ageo.fr></helios.duhamel@ageo.fr></pascal.petit@ageo.fr></antoine.giacomotto@ageo.fr></emilie.piccinotti@ageo.fr></gautier.antoine@ageo.fr></quentin.moreau@ageo.fr>",
