const isDev = process.env.NODE_ENV === "development"

/**
 * Retourne la dernire date de mise  jour des donnes
 * @param {Token} token
 * @returns {Promise<string>}
 */
export const fetchDate = async token => {
  if (isDev) {
    await new Promise(resolve => setTimeout(resolve, 1500))
    return moment().format("YYYY-MM-DD")
  }
  const response = await fetch(`/api/projects/date`, {
    method: "GET",
    headers: { Authorization: `Bearer ${token}` },
  })

  if (response.ok) {
    return await response.json()
  }

  return null
}

export const fetchAll = async token => {
  if (isDev) {
    await new Promise(resolve => setTimeout(resolve, 1500))
    return SAMPLE.PROJECTS
  }
  const response = await fetch(`/api/projects`, {
    method: "GET",
    headers: { Authorization: `Bearer ${token}` },
  })

  if (response.ok) {
    return await response.json()
  }

  return null
}

export const post = async (token, data) => {
  if (isDev) {
    await new Promise(resolve => setTimeout(resolve, 1500))
    return {
      ...data,
      users: data.users ?? [],
      developers: data.developers ?? [],
    }
  }
  const response = await fetch(`/api/projects`, {
    method: "POST",
    body: JSON.stringify(data),
    headers: { Authorization: `Bearer ${token}` },
  })

  if (response.ok) {
    return await response.json()
  }

  return null
}

export const put = async (token, id, data) => {
  // console.log("API | PROJECTS | PUT | 1")
  if (isDev) {
    await new Promise(resolve => setTimeout(resolve, 1500))
    // console.log("API | PROJECTS | PUT | 2", { ...data, id })
    return { ...data, id }
  }

  const _data = {}
  for (const [key, value] of Object.entries(data)) {
    if (!["date_end", "date_start"].includes(key) && value === null) continue
    _data[key] = value
  }

  const response = await fetch(`/api/projects/${id}`, {
    method: "PUT",
    body: JSON.stringify(_data),
    headers: { Authorization: `Bearer ${token}` },
  })

  if (response.ok) {
    return await response.json()
  }

  return null
}

export const remove = async (token, id) => {
  if (isDev) {
    await new Promise(resolve => setTimeout(resolve, 1500))
    return { id }
  }
  const response = await fetch(`/api/projects/${id}`, {
    method: "DELETE",
    headers: { Authorization: `Bearer ${token}` },
  })

  if (response.ok) {
    return await response.json()
  }

  return null
}
